import React, { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux'
import stackOverflow from "../image/StackOverflow.png";
import Search from "../image/search.svg";
import Avatar from "./Avatar/Avatar"
import "./NavBar.css";
import { setCurrentUser } from "../action/currentUser";
// import decode from 'jwt-decode'

function NavBar() {
  
    const dispatch = useDispatch()
    var User = useSelector((state) => (state.currentUserReducer));
    const navigate = useNavigate()

    const handleLogout = () =>{
      dispatch({ type: 'LOGOUT'})
      navigate('/')
      dispatch(setCurrentUser(null))
    }

    useEffect(() => {
      // const token = User?.token
      const token = User && User.token
      // if (token) {
      //   const decodedToken = decode(token)
      //   if (decodedToken.exp * 1000 < new Date().getTime()) {
      //     handleLogout()
      //   }
      // }
      dispatch(setCurrentUser(JSON.parse(localStorage.getItem('Profile'))))
    },[dispatch])

  return (
    <nav className="main-nav">
        <div className="navbar">
        <Link to="/"  className="nav-item nav-stackOverflow">
        <img src={stackOverflow} width={200} height={200} className="stack-logo"/>
        </Link>
        <Link to="/" className="nav-item nav-btn" >About</Link>
        <Link to="/" className="nav-item nav-btn" >Products</Link>
        <Link to="/" className="nav-item nav-btn" >For Teams</Link>
        <form>
            <input type="text" placeholder='Search...'/> 
            <img src={Search} width={18} className="search-icon"/>
        </form>
        { User === null ? 
            <Link to='/Auth' className='nav-item nav-links'>Log in</Link>:
            <>
            <Avatar backgroundColor='#009dff' px="10px" py="7px" borderRadius="50%" color='white'><Link to={`/User/${User.result && User.result._id}`} style={{color:"white", textDecoration:'none'}}>{User.result.name.charAt(0).toUpperCase()}</Link></Avatar>
            <button className='nav-item nav-links' onClick={handleLogout}>Log out</button>
            </>
        }
        </div>
        
    
    </nav>
  );
}

export default NavBar;
